<template>
  <el-row class="warp">
    <el-col :span="24" class="warp-breadcrum">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '/' }"><b></b></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
    </el-col>
    <el-col :span="24" class="warp-main"
            v-loading="loading"
            element-loading-text=""
            element-loading-spinner="el-icon-loading"
            element-loading-background="rgba(0, 0, 0, 0.8)">
      <!--  -->
      <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
        <el-form :inline="true" class="demo-form-inline">
            <el-input :placeholder="placeholder" v-model="keywords" style="width: 30%;">
              <el-select class="sel-placeholder" v-model="select" @change="searchFieldChange" slot="prepend" style="width:130px">
                <el-option label="ID" value="code"></el-option>
                <el-option label="" value="name"></el-option>
                <el-option label="" value="bailPercentage"></el-option>
                <el-option label="" value="csrTel"></el-option>
                <el-option label="" value="proxyUrl"></el-option>
              </el-select>
              <el-button slot="append" icon="el-icon-search" @click="getStores"></el-button>
            </el-input>
          <el-form-item>
            <el-button type="primary" icon="el-icon-plus" @click="dialogCreateVisible = true"></el-button>
          </el-form-item>
        </el-form>
      </el-col>

      <!-- -->
      <el-table :data="stores"
                style="width: 100%"
                height="680"
                :default-sort = "{prop: 'bailPercentage', order: 'descending'}"
                @selection-change="tableSelectionChange">
        <el-table-column prop="code" label="ID"></el-table-column>
        <el-table-column prop="name" label=""></el-table-column>
        <el-table-column sortable prop="bailPercentage" label=""></el-table-column>
        <el-table-column prop="csrTel" label=""></el-table-column>
        <el-table-column prop="proxyUrl" label=""></el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <el-button
              size="mini" type="primary"
              @click="viewChannel(scope.row)">
            </el-button>
          </template>
        </el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <el-button
              size="mini"
              @click="setCurrent(scope.row)">
            </el-button>
          </template>
        </el-table-column>
      </el-table>

      <!-- -->
      <el-dialog title="" center v-model="dialogCreateVisible" :visible.sync="dialogCreateVisible" :close-on-click-modal="false" @close="reset" >
        <el-form id="#create" :model="create" :rules="rules" ref="create" label-width="120px">
          <el-form-item label="" prop="name">
            <el-input v-model="create.name"></el-input>
          </el-form-item>
          <el-form-item label="appId" prop="appId">
            <el-input v-model="create.appId"></el-input>
          </el-form-item>
          <el-form-item label="" prop="bailPercentage">
            <el-input v-model="create.bailPercentage"></el-input>
          </el-form-item>
          <el-form-item label="" prop="csrTel">
            <el-input v-model="create.csrTel"></el-input>
          </el-form-item>
          <el-form-item label="" prop="proxyUrl">
            <el-input v-model="create.proxyUrl"></el-input>
          </el-form-item>
          <el-form-item prop="agent" label="">
            <el-select v-model="create.agentId" placeholder="">
              <el-option
                v-for="item in agentsInfo"
                :key="item[0]"
                :label="item[3]"
                :value="item[0]">
              </el-option>
            </el-select>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="dialogCreateVisible = false"> </el-button>
          <el-button type="primary" :loading="createLoading" @click="createStore"> </el-button>
        </div>
      </el-dialog>

      <!-- -->
      <el-dialog title="" v-model="dialogUpdateVisible" :visible.sync="dialogUpdateVisible" :close-on-click-modal="false">
        <el-form id="#update" :model="update" :rules="rules" ref="update" label-width="120px">
          <el-form-item label="" prop="name">
            <el-input v-model="update.name"></el-input>
          </el-form-item>
          <el-form-item label="appId" prop="appId">
            <el-input v-model="update.appId"></el-input>
          </el-form-item>
          <el-form-item label="" prop="bailPercentage">
            <el-input v-model="update.bailPercentage"></el-input>
          </el-form-item>
          <el-form-item label="" prop="dailyLimit">
            <el-input v-model="update.dailyLimit"></el-input>
          </el-form-item>
          <el-form-item label="" prop="csrTel">
            <el-input v-model="update.csrTel"></el-input>
          </el-form-item>
          <el-form-item label="" prop="proxyUrl">
            <el-input v-model="update.proxyUrl"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="dialogUpdateVisible = false"> </el-button>
          <el-button type="primary" :loading="updateLoading" @click="updateStore"> </el-button>
        </div>
      </el-dialog>

      <el-pagination class="paging"
                     :current-page="filter.currentPage"
                     :page-sizes="[10, 20, 50, 100]"
                     :page-size="filter.pageSize"
                     layout="total, sizes, prev, pager, next, jumper"
                     :total="totalRows"
                     @size-change="pageSizeChange"
                     @current-change="pageCurrentChange">
      </el-pagination>

    </el-col>

  </el-row>
</template>

<script>
  let placeholders = {
    "name":"",
    "bailPercentage":"",
    "code":"ID",
    "csrTel":"",
    "proxyUrl":"",
  };

  export default {
    data: function() {
      return {
        userInfo:{
          id:'',
          account:'',
          name:''
        },
        agentsInfo:{},
        stores: [],
        create: {
          name: '',
          bailPercentage: '',
          code: '',
          csrTel: '',
          proxyUrl: '',
          agentId: ''
        },
        update:{
          id: '',
          name: '',
          bailPercentage: '',
          code: '',
          csrTel: '',
          proxyUrl: '',
          dailyLimit: ''
        },
        rules: {
          appId: [
            { required: true, message: 'appId', trigger: 'blur' },
            { pattern:/^[0-9]*$/, message: 'appId'}
          ],
          name: [
            { required: true, message: '', trigger: 'blur' },
          ],
          bailPercentage: [
            { required: true, message: '', trigger: 'blur' },
            { pattern:/^[0-9]*$/, message: ''}
          ],
          code: [
            { required: true, message: 'ID', trigger: 'blur' },
          ],
          csrTel: [
            { required: true, message: '', trigger: 'blur' },
          ],
          proxyUrl: [
            { required: true, message: '', trigger: 'blur' },
          ],
          dailyLimit: [
            { required: true, message: '', trigger: 'blur' },
            { pattern:/^[0-9]*$/, message: ''}
          ],
        },
        filter: {
          pageSize: 10,
          currentPage: 1,
          beginIndex: 0,
          name: '',
          bailPercentage: '',
          code: '',
          csrTel: '',
          proxyUrl: ''
        },
        totalRows: 0,
        keywords: '', //
        select: 'name', //
        loading: true,
        selected: [], //
        dialogCreateVisible: false, //
        dialogUpdateVisible: false, //
        createLoading: false,
        updateLoading: false,
        placeholder:placeholders["name"]
      };
    },
    mounted: function() {
      this.agentsInfo = JSON.parse(sessionStorage.getItem('agentsInfo'));
      let userInfo = sessionStorage.getItem('access-user');
      if (userInfo) {
        userInfo = JSON.parse(userInfo);
        this.userInfo.id = userInfo.id;
        this.userInfo.account = userInfo.account;
        this.userInfo.name = userInfo.name;
      }
      this.getStores()
    },
    methods: {
      tableSelectionChange(val) {
        this.selected = val;
      },
      searchFieldChange(val) {
        this.select = val;
        this.placeholder=placeholders[val];
        console.log(` ${val} `);
      },
      pageSizeChange(val) {
        console.log(` ${val} `);
        this.filter.pageSize = val;
        this.getStores();
      },
      pageCurrentChange(val) {
        console.log(`: ${val}`);
        this.filter.currentPage = val;
        this.getStores();
      },
      // 
      reset() {
        this.$refs.create.resetFields();
      },
      //
      getStores() {
        this.loading = true;
        this.$http.get(`http://106.14.47.193/xpay/admin/${this.userInfo.id}/stores`).then(res => {
          this.stores = res.data.data;
          //
          let queryData = [];
          if(this.keywords !==""){
            for (var i=0,lenI=this.stores.length;i<lenI;i++) {
              let reg = new RegExp(this.keywords);
              if(this.stores[i][this.select].toString().match(reg)){
                queryData.push(this.stores[i]);
              }
            }
          }
          else queryData = this.stores;
          this.totalRows = queryData.length;
          //
          this.filter.beginIndex = (this.filter.currentPage-1)*10;
          this.stores = queryData.splice(this.filter.beginIndex,this.filter.pageSize);
          this.loading = false;
          this.selected.splice(0,this.selected.length);
        })
      },

      // 
      createStore(){
        this.$refs.create.validate((valid) => {
          if (valid) {
            this.createLoading = true;
            this.$http.put(`http://106.14.47.193/xpay/admin/${this.userInfo.id}/stores`,this.create).then(res => {
              this.$message.success('');
              this.dialogCreateVisible = false;
              this.createLoading = false;
              this.reset();
              this.getStores();
            }).catch(() =>{
              this.$message.error('');
              this.reset();
              this.createLoading = false;
            })
          }
          else {
            return false;
          }
        });
      },

      setCurrent(row){
        console.log(row);
        this.update.id=row.id;
        this.update.name=row.name;
        this.update.bailPercentage=row.bailPercentage;
        this.update.code=row.code;
        this.update.csrTel=row.csrTel;
        this.update.proxyUrl=row.proxyUrl;
        this.update.dailyLimit=row.dailyLimit;
        this.dialogUpdateVisible=true;
      },
      //
      updateStore(){
        this.$refs.update.validate((valid) => {
          if (valid) {
            this.updateLoading=true;
            this.$http.patch(`http://106.14.47.193/xpay/admin/${this.userInfo.id}/stores/${this.update.id}`).then(res => {
              row = res.data.data;
              console(row);
              this.$message.success('');
              this.dialogUpdateVisible = false;
              this.updateLoading = false;
              this.getStores();
            }).catch(() =>{
              this.$message.error('');
              this.dialogUpdateVisible = false;
              this.updateLoading = false;
            })
          }
          else {
            return false;
          }
        });
      },

      //
      viewChannel(row){
        if(row.channels){
          sessionStorage.setItem('channelData', JSON.stringify(row.channels));
          this.$router.push({ path: '/store/storeChannels' });
        }
        else {
          this.$message.error('')
        }
     }
    }
  }
</script>

<style>
  .paging{
    text-align: center;
    margin:12px 0;
  }
  .btn-edit{
    float: right;
  }

</style>



