<template>
  <el-row class="warp">
    <el-col :span="24" class="warp-breadcrum">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '/' }"><b></b></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
    </el-col>
    <el-col :span="22" class="warp-main">
      <el-form ref="formCreate" :model="formCreate" :rules="rules"  label-width="100px">
        <el-form-item prop="account" label="">
          <el-input v-model="formCreate.account"></el-input>
        </el-form-item>
        <el-form-item prop="password" label="">
          <el-input v-model="formCreate.password"></el-input>
        </el-form-item>
        <el-form-item prop="name" label="">
          <el-input v-model="formCreate.name"></el-input>
        </el-form-item>
        <el-form-item prop="agent" label="">
          <el-select v-model="formCreate.agentId" @change="onChange" placeholder="">
            <el-option
              v-for="item in agentsInfo"
              :key="item[0]"
              :label="item[3]"
              :value="item[0]">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item prop="store" label="">
          <el-select v-model="formCreate.storeId" placeholder="">
            <el-option
              v-for="item in tempStoresInfo"
              :key="item[0]"
              :label="item[1]"
              :value="item[0]">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item prop="role" label="">
          <el-select v-model="formCreate.role" placeholder="">
            <el-option label="" value="AGENT"></el-option>
            <el-option label="" value="STORE"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="onSubmit"></el-button>
        </el-form-item>
      </el-form>
    </el-col>
  </el-row>
</template>

<script>
  export default {
    data() {
      return {
        formCreate: {
          account: '',
          password: '',
          name: '',
          agentId: '',
          storeId: '',
          role: ''
        },
        agentsInfo:{},
        tempStoresInfo:{},
        rules: {
          account: [
            { required: true, message: '', trigger: 'blur' },
          ],
          password: [
            { required: true, message: '', trigger: 'blur' },
          ],
          name: [
            { required: true, message: '', trigger: 'blur' },
          ],
          role: [
            { required: true, message: '', trigger: 'blur' },
          ]
        },
      }
    },
    mounted: function(){
      this.agentsInfo = JSON.parse(sessionStorage.getItem('agentsInfo'));
      console.log(this.agentsInfo);
    },
    methods: {
      onSubmit() {
        let id = JSON.parse(sessionStorage.getItem('access-user')).id;
        this.$http.put(`http://106.14.47.193/xpay/admin/${id}/`,this.formCreate).then(() => {
          this.$message.success('');
          this.$refs.formCreate.resetFields();
        }).catch(() => {
          this.$message.error('');
          this.$refs.formCreate.resetFields();
        })
      },
      onChange() {
        let storesInfo = JSON.parse(sessionStorage.getItem('storesInfo'));
        this.tempStoresInfo = storesInfo.filter(info => {
          if(this.formCreate.agentId === info[3]){
            return info;
          }
        });
        console.log(this.tempStoresInfo);
      }
    }
  }
</script>

<style>

</style>



