<template>
  <el-row class="warp">
    <el-col :span="24" class="warp-breadcrum">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '/' }"><b></b></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
        <el-breadcrumb-item :to="{ path: '/store/storeList' }"></el-breadcrumb-item>
        <el-breadcrumb-item :to="{ path: '/store/storeChannels' }"></el-breadcrumb-item>
      </el-breadcrumb>
    </el-col>
    <el-col :span="24" class="warp-main"
            v-loading="loading"
            element-loading-text=""
            element-loading-spinner="el-icon-loading"
            element-loading-background="rgba(0, 0, 0, 0.8)">
      <!--  -->
      <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
        <el-form :inline="true" class="demo-form-inline">
          <el-input :placeholder="placeholder" v-model="keywords" style="width: 35%">
            <el-select class="sel-placeholder" v-model="select" @change="searchFieldChange" slot="prepend" style="width:130px">
              <el-option label="ID" value="extStoreId"></el-option>
              <el-option label="" value="extStoreName"></el-option>
              <el-option label="" value="paymentGateway"></el-option>
              <el-option label="" value="updateDate"></el-option>
            </el-select>
            <el-button slot="append" icon="el-icon-search" @click="getChannels"></el-button>
          </el-input>
          <el-form-item>
            <el-button type="primary" icon="el-icon-plus" @click="dialogCreateVisible = true"></el-button>
          </el-form-item>
        </el-form>
      </el-col>

      <!-- -->
      <el-table :data="channels"
                style="width: 100%"
                height="680"
                :default-sort = "{prop: 'updateDate', order: 'descending'}"
                @selection-change="tableSelectionChange">
        <el-table-column prop="extStoreId" label="ID"></el-table-column>
        <el-table-column prop="extStoreName" label=""></el-table-column>
        <el-table-column prop="paymentGateway" label=""></el-table-column>
        <el-table-column sortable prop="updateDate" label=""></el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <el-button
              size="mini"
              type="danger"
              @click="removeChannel(scope.$index, scope.row)"></el-button>
          </template>
        </el-table-column>
      </el-table>

      <!-- -->
      <el-dialog title="" center v-model="dialogCreateVisible" :visible.sync="dialogCreateVisible" :close-on-click-modal="false" @close="reset" >
        <el-form id="#create" :model="create" :rules="rules" ref="create" label-width="100px">
          <el-form-item label="ID" prop="extStoreId">
            <el-input v-model="create.extStoreId"></el-input>
          </el-form-item>
          <el-form-item label="" prop="extStoreName">
            <el-input v-model="create.extStoreName"></el-input>
          </el-form-item>
          <el-form-item label="" prop="paymentGateway">
            <el-select v-model="create.paymentGateway" placeholder="">
              <el-option label="UPAY" value="UPAY"></el-option>
              <el-option label="CHINAUMSH5" value="CHINAUMSH5"></el-option>
            </el-select>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="dialogCreateVisible = false"> </el-button>
          <el-button type="primary" :loading="createLoading" @click="createChannel"> </el-button>
        </div>
      </el-dialog>

      <el-pagination class="paging"
                     :current-page="filter.currentPage"
                     :page-sizes="[10, 20, 50, 100]"
                     :page-size="filter.pageSize"
                     layout="total, sizes, prev, pager, next, jumper"
                     :total="totalRows"
                     @size-change="pageSizeChange"
                     @current-change="pageCurrentChange">
      </el-pagination>

    </el-col>

  </el-row>
</template>

<script>
  let placeholders = {
    "extStoreId": "ID",
    "extStoreName": "",
    "paymentGateway": "",
    "updateDate": ""
  };

  export default {
    data: function() {
      return {
        userInfo:{
          id:'',
          account:'',
          name:''
        },
        channels: [],
        create: {
          extStoreId: "",
          extStoreName: "",
          paymentGateway: "",
          updateDate: ""
        },
        rules: {
          extStoreId: [
            { required: true, message: 'ID', trigger: 'blur' },
            { pattern:/^[0-9]*/, message: 'ID'}
          ],
          extStoreName: [
            { required: true, message: '', trigger: 'blur' },
          ],
          paymentGateway: [
            { required: true, message: '', trigger: 'blur' },
          ]
        },
        filter: {
          pageSize: 10,
          currentPage: 1,
          beginIndex: 0,
          extStoreId: "",
          extStoreName: "",
          paymentGateway: "",
          updateDate: "",
        },
        totalRows: 0,
        keywords: '', //
        select: 'extStoreId', //
        loading: false,
        selected: [], //
        dialogCreateVisible: false, //
        createLoading: false,
        placeholder:placeholders["extStoreId"]
      };
    },
    mounted: function() {
      var userInfo = sessionStorage.getItem('access-user');
      if (userInfo) {
        userInfo = JSON.parse(userInfo);
        this.userInfo.id = userInfo.id;
        this.userInfo.account = userInfo.account;
        this.userInfo.name = userInfo.name;
      }
      this.getChannels()
    },
    destroyed: function () {
      sessionStorage.removeItem('channelData');
    },
    methods: {
      tableSelectionChange(val) {
        this.selected = val;
      },
      searchFieldChange(val) {
        this.select = val;
        console.log(this.select);
        this.placeholder = placeholders[val];
        console.log(` ${val} `);
      },
      pageSizeChange(val) {
        console.log(` ${val} `);
        this.filter.pageSize = val;
        this.getChannels();
      },
      pageCurrentChange(val) {
        console.log(`: ${val}`);
        this.filter.currentPage = val;
        this.getChannels();
      },
      // 
      reset() {
        this.$refs.create.resetFields();
      },
      //
      getChannels() {
        this.loading = true;
        this.channels = JSON.parse(sessionStorage.getItem('channelData'));
        //
        let queryData = [];
        if(this.keywords !==""){
          for (var i=0,len=this.channels.length;i<len;i++) {
            let reg = new RegExp(this.keywords);
            if(this.channels[i][this.select].toString().match(reg)!==null){
              queryData.push(this.channels[i]);
              //console.log(queryData);
            }
          }
        }
        else queryData = this.channels;
        this.totalRows = queryData.length;
        //
        this.filter.beginIndex = (this.filter.currentPage-1)*10;
        this.channels = queryData.splice(this.filter.beginIndex,this.filter.pageSize);
        this.loading = false;
        this.selected.splice(0,this.selected.length);
      },
      // 
      createChannel(){
        this.$refs.create.validate((valid) => {
          if (valid) {
            this.createLoading = true;
            this.$http.put(`http://106.14.47.193/xpay/admin/${this.userInfo.id}/channels`).then(res => {
              console.log(res);
              this.$message.success('');
              this.dialogCreateVisible = false;
              this.createLoading = false;
              this.reset();
              this.getChannels();
            })
          }
          else {
            return false;
          }
        });
      },
      // 
      removeChannel(index,row) {
        console.log(index, row);
        this.$confirm(' ' + row.extStoreName + ', ?', '', { type: 'warning' })
          .then(() => {
            // 
            let channelId = row.id  ;
            this.$http.delete(`http://106.14.47.193/xpay/admin/${this.userInfo.id}/${channelId}`).then(() => {
              this.$message.success('' + row.extStoreName + '!');
              this.getChannels();
            })
              .catch(() => {
                this.$message.error('!');
              });
          })
          .catch(() => {
            this.$message.info('!');
          });
      }
    }
  }
</script>

<style>
  .paging{
    text-align: center;
    margin:12px 0;
  }
  .btn-edit{
    float: right;
  }

</style>



